.social-navigation ul, .legal-navigation ul, .main-navigation ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.page-template-default #wrapper ul, .panel ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-template-default #wrapper ul li, .panel ul li {
  padding-left: 3.5rem;
  position: relative;
  margin-bottom: 1.5rem;
}
.page-template-default #wrapper ul li:before, .panel ul li:before {
  position: absolute;
  top: 12px;
  left: 0;
  content: "";
  display: block;
  height: 2px;
  width: 2.4rem;
  background-color: #0a2035;
}

* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden !important;
  font-size: 62.5%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  overflow-x: hidden !important;
  -webkit-font-smoothing: antialiased;
  background-color: var(--color-secondary);
  color: var(--color-white);
}

p {
  margin-bottom: 2rem;
}

strong {
  font-weight: 600;
}

a {
  color: var(--color-black);
  text-decoration: none;
  transition: all 0.3s;
}
a:hover {
  color: var(--color-black);
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

h1 {
  margin-bottom: 4rem;
}

h2 {
  margin-bottom: 4rem;
}

h3 {
  margin-bottom: 4rem;
}

h4 {
  margin-bottom: 4rem;
}

h5 {
  margin-bottom: 4rem;
}

h6 {
  margin-bottom: 4rem;
}

.svg-icon-inline, .svg-icon {
  align-items: center;
  width: 1em;
  height: 1em;
  background-color: transparent;
  fill: currentColor;
}

.svg-icon {
  display: flex;
}

.svg-icon-inline {
  display: inline-flex;
}

@media (max-width: 767px) {
  .hide-mobile {
    display: none !important;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .hide-ipad {
    display: none !important;
  }
}

@media (min-width: 992px) {
  .hide-desktop {
    display: none !important;
  }
}

@media (max-width: 991px) {
  .reverse-mobile .columns {
    flex-direction: column-reverse;
  }
}

@media (min-width: 992px) {
  .reverse-desktop .columns {
    flex-direction: row-reverse;
  }
}

@media (min-width: 1200px) {
