.header-burger {
  position: relative;
  cursor: pointer;
  display: block;
  width: 3rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  transition: all 0.3s;
}
@media (min-width: 992px) {
  .header-burger {
    opacity: 0;
    display: none;
  }
}
.header-burger span {
  position: absolute;
  width: 100%;
  height: 0.2rem;
  background-color: #0a2035;
  border-radius: 1px;
  transition: all 0.3s;
}
.header-burger span:first-child {
  top: 0;
  right: 0;
  width: 60%;
  transform-origin: center;
}
.header-burger span:last-child {
  bottom: 0;
  left: 0;
  transform-origin: center;
}

.menu-open .header-burger span {
  width: 100%;
}
.menu-open .header-burger span:first-child {
  transform: translate(0, 0) rotate(-45deg);
  top: 50%;
}
.menu-open .header-burger span:last-child {
  transform: translate(0, 2px) rotate(45deg);
  bottom: 50%;
}

