  font-size: 62.5%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  overflow-x: hidden !important;
  -webkit-font-smoothing: antialiased;
  background-color: var(--color-secondary);
  color: var(--color-white);
}

p {
  margin-bottom: 2rem;
}

strong {
  font-weight: 600;
}

a {
  color: var(--color-black);
  text-decoration: none;
  transition: all 0.3s;
}

a:hover {
  color: var(--color-black);
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

h1 {
  margin-bottom: 4rem;
}

h2 {
  margin-bottom: 4rem;
}

h3 {
  margin-bottom: 4rem;
}

h4 {
  margin-bottom: 4rem;
}

h5 {
  margin-bottom: 4rem;
}

h6 {
  margin-bottom: 4rem;
}

.svg-icon-inline, .svg-icon {
  align-items: center;
  width: 1em;
  height: 1em;
  background-color: transparent;
  fill: currentColor;
}

.svg-icon {
  display: flex;
}

.svg-icon-inline {
  display: inline-flex;
}

.sidenav-bg {
  content: "";
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 300;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
}
.sidenav-content {
  position: fixed;
  z-index: 400;
  top: 0;
  left: 0;
  width: 95%;
  height: 100vh;
  padding: 3rem 1rem;
  border-radius: 0 2rem 2rem 0;
  background-color: #ffffff;
  background-image: url(../../src/assets/backgrounds/bg-sidenav.svg);
  background-position: left center;
  background-repeat: no-repeat;
  transform: translateX(-100%);
  transition: all 0.3s;
}
@media (min-width: 768px) {
  .sidenav-content {
    padding: 3rem 5rem;
  }
}
@media (min-width: 992px) {
  .sidenav-content {
    width: 50%;
  }
}
.sidenav .main-navigation {
  position: absolute;
  left: 2rem;
  top: 50%;
  transform: translateY(-50%);
}
.sidenav .main-navigation ul {
  display: flex;
  flex-direction: column;
  grid-row-gap: 2rem;
  text-align: left;
}
.sidenav .main-navigation a {
  font-size: 2.2rem;
}
.menu-open .sidenav-bg {
  display: block;
}
.menu-open .sidenav-content {
  transform: translateX(0);
}

.columns {
  display: flex;
  flex-direction: column;
  row-gap: 5rem;
}
@media (min-width: 992px) {
  .columns {
    flex-direction: row;
    align-items: center;
    column-gap: 10rem;
  }
}
@media (min-width: 992px) {
  .columns .col {
    width: 50%;
  }
}

.btn-animation {
  position: relative;
  font-size: 1.5rem;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  padding-left: 2rem;
}
@media (min-width: 768px) {
  .btn-animation {
    font-size: 1.8rem;
  }
}
.btn-animation:before {
  content: "";
  display: block;
  width: 6rem;
  height: 6rem;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 2rem;
  transform: translateY(-50%);
  margin-left: -2rem;
  background: #d1f94f;
  border-radius: 3rem;
  transition: all 0.3s;
}
.btn-animation:after {
  content: "";
  display: block;
  width: 2.9rem;
  height: 1.7rem;
  background-image: url(../../src/assets/icons/icon-arrow-btn.svg);
  background-size: cover;
  margin-left: 1rem;
}
.btn-animation:hover:before {
  width: calc(100% + 2rem);
}
.btn-animation:hover:after {
  animation-name: btnAnimationArrow;
  animation-duration: 0.3s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
}

@keyframes btnAnimationArrow {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.wpcf7 p {
  margin-bottom: 0;
  padding-bottom: 1.5rem;
}
.wpcf7 br {
  display: none;
}
.wpcf7 .wpcf7-not-valid-tip {
  font-size: 1.2rem;
  line-height: 1.2;
  margin-top: 1rem;
}

.flex-row {
  display: flex;
  flex-direction: column;
}
@media (min-width: 992px) {
  .flex-row {
    flex-direction: row;
    grid-column-gap: 2rem;
  }
  .flex-row > * {
    width: 50%;
  }
}

.input {
  position: relative;
  z-index: 1;
  display: inline-block;
  margin-bottom: 0.5rem;
  width: 100%;
  vertical-align: top;
}

.input__field,
.input__label-content,
textarea {
  font-size: 1.5rem;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .input__field,
  .input__label-content,
  textarea {
    font-size: 1.6rem;
  }
}

.input__field {
  color: #0a2035;
  position: relative;
  z-index: 1000;
  display: block;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #CFDCDF;
  border-radius: 0;
  background: transparent;
  line-height: 1em;
  -webkit-appearance: none; /* for box shadows to show on iOS */
  transition: all 0.2s;
}

.input__field:focus {
  outline: none;
}

.input__label {
  position: absolute;
  top: 0;
  display: inline-block;
  z-index: 500;
  width: 100%;
  height: 100%;
  text-align: left;
  cursor: text;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.input__label-content {
  position: relative;
  display: block;
  width: 100%;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

.input_select select {
  height: 5rem;
  opacity: 0;
  transition: all 0.2s;
}
.input_select select option {
  background: #a8b5c1;
}
.input_select label {
  transition: all 0.2s;
  border: 0;
  border-bottom: 1px solid #CFDCDF;
}

.input--filled select {
  opacity: 1;
}

.input--filled .input__label-content {
  transform: translateY(-80%);
}

.input--filled .input__field {
  border-color: #051321;
}

.input__field--madoka:focus + .input__label--madoka,
.input--filled .input__label--madoka {
  cursor: default;
  pointer-events: none;
}

textarea {
  height: 150px;
  overflow: hidden;
}

input[type=submit] {
  border: 0;
}

.wpcf7-acceptance {
  font-size: 1.3rem;
}
.wpcf7-acceptance .wpcf7-list-item {
  margin: 0 0 1rem 0;
}
.wpcf7-acceptance a {
  color: #0a2035;
  text-decoration: underline;
}
.wpcf7-acceptance label {
  display: flex;
}
.wpcf7-acceptance input {
  opacity: 0;
}
.wpcf7-acceptance input:not(:checked) + .wpcf7-list-item-label::after {
  opacity: 0;
}
.wpcf7-acceptance .wpcf7-list-item-label {
  position: relative;
  padding-left: 1rem;
}
.wpcf7-acceptance .wpcf7-list-item-label:before, .wpcf7-acceptance .wpcf7-list-item-label:after {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-15px, -50%);
  content: "";
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid #d1f94f;
  border-radius: 100%;
  transition: all 0.3s;
}
.wpcf7-acceptance .wpcf7-list-item-label:after {
  border: 0;
  width: 0.9rem;
  height: 0.9rem;
  left: 0.3rem;
  opacity: 0;
  background-color: #d1f94f;
  border-radius: 0.3rem;
  opacity: 1;
}

/******** CONTACT FORM 7 *********/
div.wpcf7-response-output {
  margin: 10px 0 0;
  border: 2px solid #254564 !important;
  color: #254564 !important;
}

div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing {
  border: 2px solid #ff0000;
  color: #ff0000;
}

.wpcf7-submit {
  position: relative;
}
.wpcf7-submit .ajax-loader {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-100%, -50%);
  -ms-transform: translate(-100%, -50%);
  -moz-transform: translate(-100%, -50%);
  -webkit-transform: translate(-100%, -50%);
  -o-transform: translate(-100%, -50%);
}

