.card-value {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 40rem;
  padding: 2rem;
  border-radius: 3rem;
  background: #d2c4f5;
}
.card-value p {
  color: #0a2035;
}
.card-value img {
  max-width: 137px;
  object-fit: contain;
  align-self: flex-end;
}

.images-stack {
  position: relative;
}
.images-stack img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.images-stack img:first-child {
  position: relative;
  width: 100%;
  height: auto;
}

.accordion {
  background-color: transparent;
  cursor: pointer;
  padding: 2rem;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  transition: all 0.4s;
  border-bottom: 1px solid #E6E7E8;
}
.accordion:after {
  content: "+";
  float: right;
  margin-left: 5px;
  font-size: 3rem;
  border: 2px solid #0a2035;
  width: 3.5rem;
  height: 3.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
}
.accordion.active {
  border-color: transparent;
  background-color: #F8F8FC;
  color: #0a2035;
}
.accordion.active:after {
  content: "−";
  color: #0a2035;
  border-color: #0a2035;
}

.panel {
  padding: 0 3rem;
  max-height: 0;
  overflow: hidden;
  transition: all 0.2s ease-out;
  background-color: #F8F8FC;
}
.panel.active {
  padding: 3rem;
  max-height: 100rem;
}
.loading {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 3000;
  background-color: #0a2035;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
}
.loading img {
  max-width: 7rem;
}

.loaded .loading {
  transform: translateY(-100%);
}

.sound-wave {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
  z-index: -1;
  display: flex;
  align-items: center;
}

.sound-wave-items {
  display: flex;
  flex: 1 1 auto;
  align-items: center;
  height: 12rem;
}
@media (min-width: 768px) {
  .sound-wave-items {
    height: 25rem;
  }
}
@media (min-width: 1200px) {
  .sound-wave-items {
    height: 40rem;
  }
}
@media (min-width: 768px) {
  .sound-wave-items:nth-child(4) {
    height: 40rem;
  }
}
@media (min-width: 1200px) {
  .sound-wave-items:nth-child(4) {
    height: 50rem;
  }
}
@media (max-width: 767px) {
  .sound-wave-items:nth-child(2) {
    height: 25rem;
  }
}
.sound-wave-items .mobile-wave {
  display: none;
}
@media (min-width: 992px) {
  .sound-wave-items .mobile-wave {
    display: block;
  }
