.sound-wave-item {
  flex: 1 1 auto;
  height: 30px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10rem;
  animation: equalize 3s 0s infinite;
  margin: 0 10px;
}
@media (min-width: 768px) {
  .sound-wave-item {
    margin: 0 12px;
  }
}
@media (min-width: 992px) {
