    margin: 0 10px;
  }
}
.sound-wave-item:nth-child(1) {
  animation-delay: -1s;
}
.sound-wave-item:nth-child(2) {
  animation-delay: -1.5s;
}
.sound-wave-item:nth-child(3) {
  animation-delay: -2s;
}
.sound-wave-item:nth-child(4) {
  animation-delay: -2.5s;
}
.sound-wave-item:nth-child(5) {
  animation-delay: -3s;
}
.sound-wave-item:nth-child(6) {
  animation-delay: -2.5s;
}
.sound-wave-item:nth-child(7) {
  animation-delay: -2s;
}
.sound-wave-item:nth-child(8) {
  animation-delay: -1.5s;
}
.sound-wave-item:nth-child(9) {
  animation-delay: -1s;
}

@keyframes equalize {
  0% {
    height: 30px;
  }
  70% {
    height: 100%;
  }
  100% {
    height: 30px;
  }
}
